





























import { Component, Vue } from 'vue-property-decorator'
const userInfo = require('../username/')
declare module 'vue/types/vue' {
    interface Vue {
        [key: string]: any,
    }
}
@Component({})
export default class App extends Vue {
    userInfo: any = userInfo

    keyword: String = ''

    list: Array<any> = []

    showList: Array<any> = []

    onSearch() {
        this.showList = this.list.filter(item => {
            return ~item.name.indexOf(this.keyword)
        })
    }

    formatData() {
        this.userInfo['unoccupyName'].forEach((name: String) => {
            this.list.push({
                occupy: false,
                name: name
            })
        })
        this.userInfo['occupyName'].forEach((name: String) => {
            this.list.push({
                occupy: true,
                name: name
            })
        })
        this.showList = [...this.list]
    }
    created() {
        this.formatData()
    }
}
